
console.log('hola');
function cubeEvents(){
    var $cubeScene = document.querySelector('.scene');
    var startX = 0;
    var endX = 0;
    var startY = 0;
    var endY = 0;
    $cubeScene.addEventListener('touchstart', function(event){
        console.log(event);
        startX = event.screenX;
        startY = event.screenY;
    }, false);
    $cubeScene.addEventListener('touchend', function (event) {
        console.log(event);
        endX = event.screenX;
        endY = event.screenY;
        swipeDirection();
    })
    $cubeScene.addEventListener('click', function (event) {
        console.log(event);
    })
    function swipeDirection(){
        if(startX < endX){
            alert('Derecha');
        }
        if(startX > endX){
            alert('Izquierda');
        }
        if(startY < endY){
            alert('abajo');
        }
        if(startY > endY){
            alert('arriba');
        }

    }
}



